import React, { useState, useEffect } from 'react';
import { Client, Account, Functions } from 'https://esm.sh/appwrite@14.0.1';
import { APPWRITE_ENDPOINT, APPWRITE_PROJECT_ID } from '../../utils/config';

const client = new Client().setEndpoint(APPWRITE_ENDPOINT).setProject(APPWRITE_PROJECT_ID);
const account = new Account(client);
const functions = new Functions(client);

function TeamView({ showNotification }) {
    const [teamMembers, setTeamMembers] = useState([]);
    const [loading, setLoading] = useState(true);
    const [inviteEmail, setInviteEmail] = useState('');
    const [inviteRole, setInviteRole] = useState('viewer');

    const fetchTeamMembers = async () => {
        setLoading(true);
        try {
            // Appwrite doesn't have a direct team list function for client-side
            // This needs a backend function for security.
            // For now, we'll mock this or assume a function exists.
            // const result = await functions.execute('listTeamMembers');
            // setTeamMembers(JSON.parse(result.response));
            showNotification("Team management vereist een backend functie die nog niet is gemplementeerd.", "info");
            setTeamMembers([]); // Mock data
        } catch (error) {
            console.error("Failed to fetch team members:", error);
            showNotification("Kon teamleden niet ophalen.", "error");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchTeamMembers();
    }, []);

    const handleInvite = async (e) => {
        e.preventDefault();
        try {
            // This also requires a backend function to handle invitations securely
            // await functions.execute('inviteUser', JSON.stringify({ email: inviteEmail, role: inviteRole }));
            showNotification(`${inviteEmail} is uitgenodigd als ${inviteRole}.`, "success");
            setInviteEmail('');
        } catch (error) {
            console.error("Failed to invite user:", error);
            showNotification(`Uitnodiging mislukt: ${error.message}`, "error");
        }
    };

    return (
        <div>
            <div className="mb-6">
                <h2 className="text-3xl font-bold text-gray-800">Team Management</h2>
                <p className="text-sm text-gray-500 mt-1">Beheer de rollen van je teamleden.</p>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                <div className="bg-white p-6 rounded-lg shadow-md">
                    <h3 className="text-xl font-semibold mb-4">Nodig nieuw lid uit</h3>
                    <form onSubmit={handleInvite} className="space-y-4">
                        <input
                            type="email"
                            placeholder="Emailadres"
                            value={inviteEmail}
                            onChange={(e) => setInviteEmail(e.target.value)}
                            className="w-full p-2 border rounded"
                            required
                        />
                        <select
                            value={inviteRole}
                            onChange={(e) => setInviteRole(e.target.value)}
                            className="w-full p-2 border rounded"
                        >
                            <option value="viewer">Viewer</option>
                            <option value="editor">Editor</option>
                        </select>
                        <button type="submit" className="w-full bg-indigo-600 text-white px-4 py-2 rounded-lg shadow hover:bg-indigo-700">
                            Verstuur Uitnodiging
                        </button>
                    </form>
                </div>
                <div className="bg-white p-6 rounded-lg shadow-md">
                    <h3 className="text-xl font-semibold mb-4">Huidige Teamleden</h3>
                    {loading ? <p>Laden...</p> : (
                        <ul className="divide-y divide-gray-200">
                           {teamMembers.length > 0 ? teamMembers.map(member => (
                                <li key={member.id} className="py-3 flex justify-between items-center">
                                    <div>
                                        <p className="font-medium">{member.name}</p>
                                        <p className="text-sm text-gray-500">{member.email}</p>
                                    </div>
                                    <span className="capitalize bg-gray-200 text-gray-800 text-xs font-medium px-2.5 py-0.5 rounded-full">{member.role}</span>
                                </li>
                            )) : <p className="text-gray-500">Geen teamleden gevonden.</p>}
                        </ul>
                    )}
                </div>
            </div>
        </div>
    );
}

export default TeamView;
