import React, { useState, useEffect } from 'react';
import { Client, Account, Databases, ID, Functions } from 'appwrite';

// Component Imports
import Sidebar from './components/Sidebar';
import VolunteersView from './components/views/VolunteersView';
import ContactsView from './components/views/ContactsView';
import CompaniesView from './components/views/CompaniesView';
import PerformancesView from './components/views/PerformancesView';
import LocationsView from './components/views/LocationsView';
import ExecutionsView from './components/views/ExecutionsView';
import EventsView from './components/views/EventsView';
import InfoView from './components/views/InfoView';
import NieuwsView from './components/views/NieuwsView';
import ToegankelijkheidView from './components/views/ToegankelijkheidView';
import ScheduleView from './components/views/ScheduleView';
import ContractGenerator from './components/views/ContractGenerator';
import TeamView from './components/views/TeamView';
import LoginScreen from './components/LoginScreen';
import Notification from './components/ui/Notification';
import ConfirmModal from './components/ui/ConfirmModal';
import QuickViewModal from './components/ui/QuickViewModal';

// Util & Config Imports
import { icons } from './utils/icons';
import { fetchAllDocuments } from './utils/api';
import { 
    APPWRITE_ENDPOINT, APPWRITE_PROJECT_ID, APPWRITE_DATABASE_ID,
    APPWRITE_COLLECTION_CONTACTS_ID, APPWRITE_COLLECTION_COMPANIES_ID,
    APPWRITE_COLLECTION_PERFORMANCES_ID, APPWRITE_COLLECTION_LOCATIONS_ID,
    APPWRITE_COLLECTION_EXECUTIONS_ID, APPWRITE_COLLECTION_EVENTS_ID,
    APPWRITE_COLLECTION_INFO_ID, APPWRITE_COLLECTION_NEWS_ID,
    APPWRITE_COLLECTION_ACCESSIBILITY_ID, SUPER_ADMIN_EMAIL
} from './utils/config';

// --- Initialiseer Appwrite Client ---
const client = new Client();
client.setEndpoint(APPWRITE_ENDPOINT).setProject(APPWRITE_PROJECT_ID);
const account = new Account(client);
const databases = new Databases(client);

function App() {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    const init = async () => {
      setLoading(true);
      try {
        const currentUser = await account.get();
        if (currentUser.email.endsWith('@cafetheaterfestival.nl')) {
          const prefs = await account.getPrefs();
          const role = currentUser.email === SUPER_ADMIN_EMAIL ? 'super_admin' : (prefs.role || 'viewer');
          setUser({ ...currentUser, role: role });
        } else {
          await account.deleteSession('current');
          setError('Toegang geweigerd. Alleen accounts van @cafetheaterfestival.nl zijn toegestaan.');
          setUser(null);
        }
      } catch (err) {
        setUser(null);
      } finally {
        window.history.replaceState(null, '', window.location.pathname);
        setLoading(false);
      }
    };
    init();
  }, []);

  const handleGoogleLogin = async () => {
    try {
        const redirectUrl = window.location.origin + window.location.pathname;
        account.createOAuth2Session('google', redirectUrl, redirectUrl);
    } catch (e) {
        console.error("Google login error", e);
        setError("Inloggen met Google is mislukt.");
    }
  };

  const handleLogout = async () => {
    try {
      await account.deleteSession('current');
      setUser(null);
    } catch (e) {
      console.error("Logout error", e);
    }
  };

  if (loading) {
    return <div className="flex items-center justify-center h-screen bg-gray-100"><div className="text-xl font-semibold">Sessie controleren...</div></div>;
  }

  if (!user) {
    return <LoginScreen onLogin={handleGoogleLogin} error={error} />;
  }

  return <CrmApp user={user} onLogout={handleLogout} />;
}


function CrmApp({ user, onLogout }) {
  const [activeView, setActiveView] = useState('contacts');
  const [contacts, setContacts] = useState([]);
  const [companies, setCompanies] = useState([]);
  const [performances, setPerformances] = useState([]);
  const [locations, setLocations] = useState([]);
  const [executions, setExecutions] = useState([]);
  const [events, setEvents] = useState([]);
  const [infoItems, setInfoItems] = useState([]);
  const [nieuwsItems, setNieuwsItems] = useState([]);
  const [toegankelijkheidItems, settoegankelijkheidItems] = useState([]);

  const [loadingData, setLoadingData] = useState(true);
  const [notification, setNotification] = useState({ show: false, message: '', type: 'success' });
  const [confirmModal, setConfirmModal] = useState({ show: false, message: '', onConfirm: () => {} });
  const [isMobileNavOpen, setIsMobileNavOpen] = useState(false);
  const [quickViewItem, setQuickViewItem] = useState(null);

  const showNotification = (message, type = 'success') => {
    setNotification({ show: true, message, type });
    setTimeout(() => setNotification({ show: false, message: '', type: 'success' }), 3000);
  };
  
  const showConfirm = (message, onConfirm) => {
    setConfirmModal({ show: true, message, onConfirm });
  };
  
  const hideConfirm = () => {
    setConfirmModal({ show: false, message: '', onConfirm: () => {} });
  };

  useEffect(() => {
    setLoadingData(true);
    const collections = {
      contacts: { id: APPWRITE_COLLECTION_CONTACTS_ID, setter: setContacts },
      companies: { id: APPWRITE_COLLECTION_COMPANIES_ID, setter: setCompanies },
      performances: { id: APPWRITE_COLLECTION_PERFORMANCES_ID, setter: setPerformances },
      locations: { id: APPWRITE_COLLECTION_LOCATIONS_ID, setter: setLocations },
      executions: { id: APPWRITE_COLLECTION_EXECUTIONS_ID, setter: setExecutions },
      events: { id: APPWRITE_COLLECTION_EVENTS_ID, setter: setEvents },
      info: { id: APPWRITE_COLLECTION_INFO_ID, setter: setInfoItems },
      news: { id: APPWRITE_COLLECTION_NEWS_ID, setter: setNieuwsItems },
      accessibility: { id: APPWRITE_COLLECTION_ACCESSIBILITY_ID, setter: settoegankelijkheidItems },
    };

    const fetchInitialData = async () => {
        try {
            const promises = Object.values(collections).map(c => fetchAllDocuments(databases, c.id));
            const results = await Promise.all(promises);
            const mapDocs = (docs) => docs.map(doc => ({ ...doc, id: doc.$id }));
            
            Object.keys(collections).forEach((key, index) => {
                collections[key].setter(mapDocs(results[index]));
            });

        } catch (e) {
            console.error("Fout bij ophalen data:", e);
            showNotification("Kon data niet laden.", "error");
        }
    };
    
    fetchInitialData().finally(() => setLoadingData(false));

    const unsubscribers = Object.values(collections).map(({ id, setter }) => {
        const channel = `databases.${APPWRITE_DATABASE_ID}.collections.${id}.documents`;
        return client.subscribe(channel, response => {
            const changedDoc = { ...response.payload, id: response.payload.$id };
            setter(prevItems => {
                const eventTypes = response.events[0];
                if (eventTypes.includes('.create')) return [...prevItems, changedDoc];
                if (eventTypes.includes('.update')) return prevItems.map(item => item.id === changedDoc.id ? changedDoc : item);
                if (eventTypes.includes('.delete')) return prevItems.filter(item => item.id !== changedDoc.id);
                return prevItems;
            });
        });
    });

    return () => unsubscribers.forEach(unsub => unsub());
  }, []);

  const handleAddItem = async (collectionId, data) => {
    try {
      await databases.createDocument(APPWRITE_DATABASE_ID, collectionId, ID.unique(), data);
      showNotification('Item succesvol toegevoegd!');
    } catch (e) {
      console.error("Fout bij toevoegen item:", e);
      showNotification(`Toevoegen mislukt: ${e.message}`, 'error');
    }
  };

  const handleBulkAddItems = async (collectionId, itemsData) => {
    try {
        await Promise.all(itemsData.map(data => 
            databases.createDocument(APPWRITE_DATABASE_ID, collectionId, ID.unique(), data)
        ));
        showNotification(`${itemsData.length} items succesvol toegevoegd!`, 'success');
    } catch (e) {
        console.error("Bulk toevoegen mislukt:", e);
        showNotification(`Bulk toevoegen mislukt: ${e.message}`, 'error');
    }
  };

  const handleUpdateItem = async (collectionId, id, data) => {
    try {
      const cleanData = Object.fromEntries(Object.entries(data).filter(([_, v]) => v !== null));
      await databases.updateDocument(APPWRITE_DATABASE_ID, collectionId, id, cleanData);
      showNotification('Item succesvol bijgewerkt!');
    } catch (e) {
      console.error("Fout bij bijwerken item:", e);
      showNotification(`Bijwerken mislukt: ${e.message}`, 'error');
    }
  };
  
  const handleBulkUpdateItems = async (collectionId, updates) => {
    showConfirm(`Weet je zeker dat je ${updates.length} items wilt bijwerken?`, async () => {
        let successCount = 0;
        let errorCount = 0;
        
        const promises = updates.map(async ({ id, data }) => {
            try {
                const cleanData = Object.fromEntries(Object.entries(data).filter(([_, v]) => v !== null));
                await databases.updateDocument(APPWRITE_DATABASE_ID, collectionId, id, cleanData);
                successCount++;
            } catch (e) {
                console.error(`Fout bij bijwerken item ${id}:`, e);
                errorCount++;
            }
        });

        await Promise.all(promises);

        if (errorCount > 0) {
            showNotification(`${successCount} items bijgewerkt, ${errorCount} mislukt.`, 'error');
        } else {
            showNotification(`${successCount} items succesvol bijgewerkt!`, 'success');
        }
        hideConfirm();
    });
  };

  const handleDeleteItem = (collectionId, id) => {
     showConfirm("Weet je zeker dat je dit item wilt verwijderen?", async () => {
        try {
            await databases.deleteDocument(APPWRITE_DATABASE_ID, collectionId, id);
            showNotification('Item succesvol verwijderd!');
        } catch (e) {
            console.error("Fout bij verwijderen item:", e);
            showNotification('Verwijderen mislukt.', 'error');
        }
        hideConfirm();
     });
  };

  const handleBulkDeleteItems = (collectionId, ids) => {
    showConfirm(`Weet je zeker dat je ${ids.length} geselecteerde items wilt verwijderen?`, async () => {
        try {
            await Promise.all(ids.map(id => databases.deleteDocument(APPWRITE_DATABASE_ID, collectionId, id)));
            showNotification(`${ids.length} items succesvol verwijderd!`);
        } catch (e) {
            console.error("Fout bij bulk verwijderen:", e);
            showNotification('Bulk verwijderen mislukt.', 'error');
        }
        hideConfirm();
    });
  };
  
  const standardizeRole = (role) => (role || '').trim().toLowerCase();
  const artists = contacts.filter(c => standardizeRole(c.Role) === 'artiest');
  const cafeOwners = contacts.filter(c => standardizeRole(c.Role) === 'caf-eigenaar');
  
  const hasEditPermissions = user.role === 'editor' || user.role === 'super_admin';
  const allData = { contacts, companies, performances, locations };

  const views = {
    volunteers: <VolunteersView contacts={contacts} onAdd={(data) => handleAddItem(APPWRITE_COLLECTION_CONTACTS_ID, data)} onUpdate={(id, data) => handleUpdateItem(APPWRITE_COLLECTION_CONTACTS_ID, id, data)} onDelete={(id) => handleDeleteItem(APPWRITE_COLLECTION_CONTACTS_ID, id)} hasEditPermissions={hasEditPermissions} />,
    contacts: <ContactsView contacts={contacts} onAdd={(data) => handleAddItem(APPWRITE_COLLECTION_CONTACTS_ID, data)} onUpdate={(id, data) => handleUpdateItem(APPWRITE_COLLECTION_CONTACTS_ID, id, data)} onBulkUpdate={(updates) => handleBulkUpdateItems(APPWRITE_COLLECTION_CONTACTS_ID, updates)} onDelete={(id) => handleDeleteItem(APPWRITE_COLLECTION_CONTACTS_ID, id)} onBulkAdd={(data) => handleBulkAddItems(APPWRITE_COLLECTION_CONTACTS_ID, data)} onBulkDelete={(ids) => handleBulkDeleteItems(APPWRITE_COLLECTION_CONTACTS_ID, ids)} hasEditPermissions={hasEditPermissions} />,
    companies: <CompaniesView companies={companies} artists={artists} onAdd={(data) => handleAddItem(APPWRITE_COLLECTION_COMPANIES_ID, data)} onUpdate={(id, data) => handleUpdateItem(APPWRITE_COLLECTION_COMPANIES_ID, id, data)} onDelete={(id) => handleDeleteItem(APPWRITE_COLLECTION_COMPANIES_ID, id)} onBulkAdd={(data) => handleBulkAddItems(APPWRITE_COLLECTION_COMPANIES_ID, data)} onBulkDelete={(ids) => handleBulkDeleteItems(APPWRITE_COLLECTION_COMPANIES_ID, ids)} hasEditPermissions={hasEditPermissions} onQuickView={setQuickViewItem} />,
    performances: <PerformancesView performances={performances} companies={companies} onAdd={(data) => handleAddItem(APPWRITE_COLLECTION_PERFORMANCES_ID, data)} onUpdate={(id, data) => handleUpdateItem(APPWRITE_COLLECTION_PERFORMANCES_ID, id, data)} onDelete={(id) => handleDeleteItem(APPWRITE_COLLECTION_PERFORMANCES_ID, id)} onBulkAdd={(data) => handleBulkAddItems(APPWRITE_COLLECTION_PERFORMANCES_ID, data)} onBulkDelete={(ids) => handleBulkDeleteItems(APPWRITE_COLLECTION_PERFORMANCES_ID, ids)} hasEditPermissions={hasEditPermissions} onQuickView={setQuickViewItem} />,
    locations: <LocationsView locations={locations} cafeOwners={cafeOwners} performances={performances} onAdd={(data) => handleAddItem(APPWRITE_COLLECTION_LOCATIONS_ID, data)} onUpdate={(id, data) => handleUpdateItem(APPWRITE_COLLECTION_LOCATIONS_ID, id, data)} onDelete={(id) => handleDeleteItem(APPWRITE_COLLECTION_LOCATIONS_ID, id)} onBulkAdd={(data) => handleBulkAddItems(APPWRITE_COLLECTION_LOCATIONS_ID, data)} onBulkDelete={(ids) => handleBulkDeleteItems(APPWRITE_COLLECTION_LOCATIONS_ID, ids)} hasEditPermissions={hasEditPermissions} onQuickView={setQuickViewItem} />,
    executions: <ExecutionsView executions={executions} performances={performances} locations={locations} onAdd={(data) => handleAddItem(APPWRITE_COLLECTION_EXECUTIONS_ID, data)} onBulkAdd={(data) => handleBulkAddItems(APPWRITE_COLLECTION_EXECUTIONS_ID, data)} onUpdate={(id, data) => handleUpdateItem(APPWRITE_COLLECTION_EXECUTIONS_ID, id, data)} onDelete={(id) => handleDeleteItem(APPWRITE_COLLECTION_EXECUTIONS_ID, id)} onBulkDelete={(ids) => handleBulkDeleteItems(APPWRITE_COLLECTION_EXECUTIONS_ID, ids)} hasEditPermissions={hasEditPermissions} onQuickView={setQuickViewItem} />,
    events: <EventsView events={events} executions={executions} performances={performances} locations={locations} onAdd={(data) => handleAddItem(APPWRITE_COLLECTION_EVENTS_ID, data)} onUpdate={(id, data) => handleUpdateItem(APPWRITE_COLLECTION_EVENTS_ID, id, data)} onDelete={(id) => handleDeleteItem(APPWRITE_COLLECTION_EVENTS_ID, id)} onBulkAdd={(data) => handleBulkAddItems(APPWRITE_COLLECTION_EVENTS_ID, data)} onBulkDelete={(ids) => handleBulkDeleteItems(APPWRITE_COLLECTION_EVENTS_ID, ids)} hasEditPermissions={hasEditPermissions} />,
    info: <InfoView infoItems={infoItems} onAdd={(data) => handleAddItem(APPWRITE_COLLECTION_INFO_ID, data)} onUpdate={(id, data) => handleUpdateItem(APPWRITE_COLLECTION_INFO_ID, id, data)} onDelete={(id) => handleDeleteItem(APPWRITE_COLLECTION_INFO_ID, id)} onBulkAdd={(data) => handleBulkAddItems(APPWRITE_COLLECTION_INFO_ID, data)} onBulkDelete={(ids) => handleBulkDeleteItems(APPWRITE_COLLECTION_INFO_ID, ids)} hasEditPermissions={hasEditPermissions} />,
    news: <NieuwsView nieuwsItems={nieuwsItems} onAdd={(data) => handleAddItem(APPWRITE_COLLECTION_NEWS_ID, data)} onUpdate={(id, data) => handleUpdateItem(APPWRITE_COLLECTION_NEWS_ID, id, data)} onDelete={(id) => handleDeleteItem(APPWRITE_COLLECTION_NEWS_ID, id)} hasEditPermissions={hasEditPermissions} />,
    accessibility: <ToegankelijkheidView toegankelijkheidItems={toegankelijkheidItems} onAdd={(data) => handleAddItem(APPWRITE_COLLECTION_ACCESSIBILITY_ID, data)} onUpdate={(id, data) => handleUpdateItem(APPWRITE_COLLECTION_ACCESSIBILITY_ID, id, data)} onDelete={(id) => handleDeleteItem(APPWRITE_COLLECTION_ACCESSIBILITY_ID, id)} hasEditPermissions={hasEditPermissions} />,
    schedule: <ScheduleView events={events} performances={performances} executions={executions} locations={locations} companies={companies} onQuickView={setQuickViewItem} />,
    contract: <ContractGenerator contacts={contacts} companies={companies} performances={performances} locations={locations} events={events} executions={executions} showNotification={showNotification} />,
    team: user.role === 'super_admin' ? <TeamView showNotification={showNotification} /> : null,
  };

  return (
    <div className="flex h-screen bg-gray-50" style={{fontFamily: 'Inter, sans-serif'}}>
      <div className="hidden md:flex md:flex-shrink-0">
          <Sidebar activeView={activeView} setActiveView={setActiveView} user={user} onLogout={onLogout} />
      </div>

      {isMobileNavOpen && (
        <div className="md:hidden fixed inset-0 z-50 animate-fade-in">
           <Sidebar 
              activeView={activeView} 
              setActiveView={(view) => {
                  setActiveView(view);
                  setIsMobileNavOpen(false);
              }} 
              user={user} 
              onLogout={onLogout} 
              isMobile={true}
              onClose={() => setIsMobileNavOpen(false)}
            />
        </div>
      )}
      
      <div className="flex-1 flex flex-col w-full overflow-hidden">
        <header className="md:hidden flex justify-between items-center p-4 bg-white border-b border-gray-200">
           <div className="flex items-center space-x-3">
             <img src="https://cafetheaterfestival.nl/wp-content/uploads/2025/06/fav.png" alt="CTF Logo" className="w-8 h-8" />
             <h1 className="text-xl font-bold text-gray-800">CTF CRM</h1>
           </div>
           <button onClick={() => setIsMobileNavOpen(true)} className="text-gray-600 p-2">
             {icons.menu}
           </button>
        </header>
        
        <main className="flex-1 p-4 sm:p-6 lg:p-8 overflow-y-auto relative">
          {notification.show && <Notification message={notification.message} type={notification.type} />}
          {confirmModal.show && <ConfirmModal message={confirmModal.message} onConfirm={confirmModal.onConfirm} onCancel={hideConfirm} />}
          
          {quickViewItem && (
            <QuickViewModal
                initialItem={quickViewItem.item}
                itemType={quickViewItem.type}
                data={allData}
                onClose={() => setQuickViewItem(null)}
            />
          )}

          {loadingData ? <div className="text-center p-10">Data laden...</div> : views[activeView]}
        </main>
      </div>
    </div>
  );
}

export default App;
