import React, { useState, useMemo, useEffect } from 'react';
import jsPDF from 'jspdf'; // Import de class
import 'jspdf-autotable'; // Deze import voegt de plugin toe

// 2. 'locations' toegevoegd aan de props om de locatienaam op te halen
function ContractGenerator({ contacts, companies, performances, locations, executions, showNotification }) {
    const [selectedCompanyId, setSelectedCompanyId] = useState('');
    const [contractDate, setContractDate] = useState(new Date().toISOString().slice(0, 10));

    const companyDetails = useMemo(() => {
        if (!selectedCompanyId) return null;
        const company = companies.find(c => c.id === selectedCompanyId);
        if (!company) return null;

        const primaryContact = contacts.find(c => c.id === company.primaryContactId);
        const companyPerformances = performances.filter(p => p.companyId === selectedCompanyId);
        const performanceExecutions = executions.filter(ex => companyPerformances.some(p => p.id === ex.performanceId));

        return { ...company, primaryContact, performances: companyPerformances, executions: performanceExecutions };
    }, [selectedCompanyId, companies, contacts, performances, executions]);

    const generatePdf = () => {
        if (!companyDetails) {
            showNotification("Selecteer eerst een gezelschap.", "error");
            return;
        }

        const doc = new jsPDF();
        
        doc.setFontSize(22);
        doc.text("Overeenkomst Caf Theater Festival", 14, 22);
        
        doc.setFontSize(11);
        doc.text(`Datum: ${new Date(contractDate).toLocaleDateString('nl-NL')}`, 14, 32);

        doc.setFontSize(12);
        doc.text("Tussen:", 14, 45);
        doc.setFontSize(11);
        doc.text("Stichting Caf Theater Festival", 14, 52);
        doc.text("Adres: Oudegracht 230, 3511 NT Utrecht", 14, 57);
        doc.text("Hierna te noemen 'Festival'", 14, 62);
        
        doc.text("En:", 14, 75);
        // 3. Eigenschap 'Name' gebruikt voor consistentie
        doc.text(companyDetails.Name, 14, 82);
        doc.text(`T.a.v. ${companyDetails.primaryContact?.Name || 'Onbekend'}`, 14, 87);
        // 4. Typo 'Adress' gecorrigeerd naar 'Address'
        doc.text(`Adres: ${companyDetails.primaryContact?.Address || 'Onbekend'}`, 14, 92);
        doc.text("Hierna te noemen 'Gezelschap'", 14, 97);
        
        doc.autoTable({
            startY: 110,
            head: [['Voorstelling', 'Uitvoeringsdata en -tijden', 'Locatie']],
            body: companyDetails.executions.map(ex => {
                const performance = companyDetails.performances.find(p => p.id === ex.performanceId);
                // 5. Locatienaam wordt nu correct opgezocht
                const location = locations.find(loc => loc.id === ex.locationId);
                return [
                    performance?.title || 'Onbekend',
                    new Date(ex.dateTime).toLocaleString('nl-NL', { dateStyle: 'short', timeStyle: 'short' }),
                    location?.Name || 'N.t.b.'
                ];
            }),
        });

        const finalY = (doc.lastAutoTable && doc.lastAutoTable.finalY) ? doc.lastAutoTable.finalY : 150;
        
        doc.setFontSize(12);
        doc.text("Vergoeding", 14, finalY + 10);
        doc.setFontSize(11);
        doc.text(`Het Festival en het Gezelschap komen een vergoeding van ${companyDetails.fee || '0,00'} overeen.`, 14, finalY + 17);
        
        doc.text("Akkoord:", 14, finalY + 40);
        doc.text("Namens Festival:", 14, finalY + 50);
        doc.text("Namens Gezelschap:", 100, finalY + 50);
        
        doc.save(`Contract_${companyDetails.Name.replace(/\s/g, '_')}.pdf`);
        showNotification("Contract succesvol gegenereerd!", "success");
    };

    return (
        <div>
            <div className="mb-6">
                <h2 className="text-3xl font-bold text-gray-800">Contract Generator</h2>
                <p className="text-sm text-gray-500 mt-1">Genereer PDF-contracten voor gezelschappen.</p>
            </div>

            <div className="bg-white p-6 rounded-lg shadow-md max-w-lg mx-auto">
                <div className="space-y-4">
                    <div>
                        <label className="block text-sm font-medium text-gray-700">Selecteer Gezelschap</label>
                        <select
                            value={selectedCompanyId}
                            onChange={(e) => setSelectedCompanyId(e.target.value)}
                            className="mt-1 block w-full pl-3 pr-10 py-2 text-base border-gray-300 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm rounded-md"
                        >
                            <option value="">-- Kies een gezelschap --</option>
                            {/* 3. Eigenschap 'Name' gebruikt voor consistentie */}
                            {companies.map(c => <option key={c.id} value={c.id}>{c.Name}</option>)}
                        </select>
                    </div>
                    <div>
                        <label className="block text-sm font-medium text-gray-700">Contractdatum</label>
                        <input
                            type="date"
                            value={contractDate}
                            onChange={(e) => setContractDate(e.target.value)}
                            className="mt-1 block w-full pl-3 pr-10 py-2 text-base border-gray-300 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm rounded-md"
                        />
                    </div>
                    <button
                        onClick={generatePdf}
                        disabled={!selectedCompanyId}
                        className="w-full bg-indigo-600 text-white px-4 py-2 rounded-lg shadow hover:bg-indigo-700 disabled:bg-gray-400"
                    >
                        Genereer PDF Contract
                    </button>
                </div>
            </div>
        </div>
    );
}

export default ContractGenerator;
