import React, { useMemo } from 'react';
import { icons } from '../../utils/icons';

function ScheduleView({ events, performances, executions, locations, companies, onQuickView }) {
    const scheduleData = useMemo(() => {
        const performanceMap = new Map(performances.map(p => {
            const company = companies.find(c => c.id === p.companyId);
            return [p.id, { ...p, companyName: company?.companyName || 'Onbekend' }];
        }));
        const locationMap = new Map(locations.map(l => [l.id, l]));

        const executionDetails = executions.map(ex => ({
            ...ex,
            performance: performanceMap.get(ex.performanceId),
            location: locationMap.get(ex.locationId),
            start: new Date(ex.dateTime),
            end: new Date(new Date(ex.dateTime).getTime() + (performanceMap.get(ex.performanceId)?.duration || 30) * 60000)
        }));

        const groupedByDay = executionDetails.reduce((acc, ex) => {
            if (!ex.start || isNaN(ex.start)) return acc;
            const day = ex.start.toLocaleDateString('nl-NL', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' });
            if (!acc[day]) {
                acc[day] = [];
            }
            acc[day].push(ex);
            return acc;
        }, {});

        Object.values(groupedByDay).forEach(dayExecutions => {
            dayExecutions.sort((a, b) => a.start - b.start);
        });

        return Object.entries(groupedByDay).sort(([dayA], [dayB]) => new Date(dayA.split(' ')[1]) - new Date(dayB.split(' ')[1]));
    }, [executions, performances, locations, companies]);

    if (scheduleData.length === 0) {
        return <div className="text-center p-10">Geen uitvoeringen gevonden om een schema te maken.</div>;
    }

    return (
        <div>
            <div className="mb-6">
                <h2 className="text-3xl font-bold text-gray-800">Blokkenschema</h2>
                <p className="text-sm text-gray-500 mt-1">Overzicht van alle uitvoeringen per dag.</p>
            </div>
            <div className="space-y-8">
                {scheduleData.map(([day, dayExecutions]) => (
                    <div key={day}>
                        <h3 className="text-xl font-bold text-indigo-700 bg-indigo-100 p-3 rounded-t-lg capitalize">{day}</h3>
                        <div className="bg-white shadow-md rounded-b-lg p-4">
                            {dayExecutions.map(ex => (
                                <div key={ex.id} className="border-b last:border-b-0 py-4 flex flex-col md:flex-row md:items-center gap-4">
                                    <div className="w-full md:w-1/4">
                                        <p className="font-semibold text-lg">{ex.start.toLocaleTimeString('nl-NL', { hour: '2-digit', minute: '2-digit' })} - {ex.end.toLocaleTimeString('nl-NL', { hour: '2-digit', minute: '2-digit' })}</p>
                                    </div>
                                    <div className="w-full md:w-2/4">
                                        <h4 className="font-bold text-lg text-gray-800">{ex.performance?.title || 'Onbekende titel'}</h4>
                                        <p className="text-sm text-gray-600">door {ex.performance?.companyName || 'Onbekend gezelschap'}</p>
                                    </div>
                                    <div className="w-full md:w-1/4">
                                        <p className="font-semibold">{ex.location?.name || 'Onbekende locatie'}</p>
                                        <p className="text-sm text-gray-500">{ex.location?.address}</p>
                                    </div>
                                    <div className="flex-shrink-0">
                                        <button onClick={() => onQuickView({ item: ex, type: 'execution' })} className="text-indigo-600 hover:text-indigo-900 p-2 rounded-full hover:bg-indigo-100">{icons.eye}</button>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}

export default ScheduleView;
