import React, { useState, useMemo } from 'react';
import { icons } from '../../utils/icons';

function EventForm({ event, executions, performances, locations, onClose, onSave }) {
    const [formData, setFormData] = useState({
        eventName: event?.eventName || '',
        executionIds: event?.executionIds || [],
    });

    const executionDetails = useMemo(() => {
        return executions.map(ex => {
            const performance = performances.find(p => p.id === ex.performanceId);
            const location = locations.find(l => l.id === ex.locationId);
            return {
                id: ex.id,
                displayText: `${performance?.title || 'N/A'} @ ${location?.name || 'N/A'} - ${new Date(ex.dateTime).toLocaleString('nl-NL')}`
            };
        });
    }, [executions, performances, locations]);

    const handleMultiSelectChange = (e) => {
        const options = e.target.options;
        const value = [];
        for (let i = 0, l = options.length; i < l; i++) {
            if (options[i].selected) {
                value.push(options[i].value);
            }
        }
        setFormData(prev => ({ ...prev, executionIds: value }));
    };

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({ ...prev, [name]: value }));
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        onSave(formData);
    };

    return (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50 p-4">
            <div className="bg-white rounded-lg p-8 w-full max-w-lg shadow-xl">
                <div className="flex justify-between items-center mb-6">
                    <h3 className="text-2xl font-bold">{event ? 'Event Bewerken' : 'Nieuw Event'}</h3>
                    <button onClick={onClose} className="text-gray-500 hover:text-gray-800">{icons.x}</button>
                </div>
                <form onSubmit={handleSubmit} className="space-y-4">
                    <input type="text" name="eventName" value={formData.eventName} onChange={handleChange} placeholder="Naam van het event" className="w-full p-2 border rounded" required />
                    <label className="block text-sm font-medium text-gray-700">Selecteer Uitvoeringen</label>
                    <select multiple name="executionIds" value={formData.executionIds} onChange={handleMultiSelectChange} className="w-full p-2 border rounded h-48">
                        {executionDetails.map(ex => <option key={ex.id} value={ex.id}>{ex.displayText}</option>)}
                    </select>
                    <div className="flex justify-end space-x-4">
                        <button type="button" onClick={onClose} className="px-4 py-2 bg-gray-200 rounded">Annuleren</button>
                        <button type="submit" className="px-4 py-2 bg-indigo-600 text-white rounded hover:bg-indigo-700">Opslaan</button>
                    </div>
                </form>
            </div>
        </div>
    );
}

export default EventForm;
