import React, { useState, useMemo } from 'react';
import { useSortAndFilter } from '../../hooks/useSortAndFilter';
import ViewHeader from '../ui/ViewHeader';
import EventForm from '../forms/EventForm';
import GenericImportModal from '../ui/GenericImportModal';
import { SortableTh } from '../ui/TableComponents';
import { icons } from '../../utils/icons';

function EventsView({ events, executions, performances, locations, onAdd, onUpdate, onDelete, onBulkAdd, onBulkDelete, hasEditPermissions }) {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isImportModalOpen, setIsImportModalOpen] = useState(false);
    const [editingEvent, setEditingEvent] = useState(null);
    const [selectedIds, setSelectedIds] = useState(new Set());

    const eventsWithDetails = useMemo(() => {
        const executionMap = new Map(executions.map(ex => {
            const performance = performances.find(p => p.id === ex.performanceId);
            const location = locations.find(l => l.id === ex.locationId);
            return [ex.id, {
                performanceTitle: performance?.title || 'Onbekend',
                locationName: location?.name || 'Onbekend',
                dateTime: new Date(ex.dateTime).toLocaleString('nl-NL')
            }];
        }));
        return events.map(ev => ({
            ...ev,
            executionDetails: (ev.executionIds || []).map(id => executionMap.get(id))
        }));
    }, [events, executions, performances, locations]);

    const {
        filteredAndSortedItems: filteredEvents,
        searchTerm,
        setSearchTerm,
        sortConfig,
        requestSort
    } = useSortAndFilter(eventsWithDetails, 'eventName', ['eventName']);

    const handleEdit = (event) => {
        setEditingEvent(event);
        setIsModalOpen(true);
    };

    const handleAddNew = () => {
        setEditingEvent(null);
        setIsModalOpen(true);
    };

    const handleSelectAll = (e) => {
        if (e.target.checked) setSelectedIds(new Set(filteredEvents.map(ev => ev.id)));
        else setSelectedIds(new Set());
    };

    const handleSelectOne = (e, id) => {
        const newSelectedIds = new Set(selectedIds);
        if (e.target.checked) newSelectedIds.add(id);
        else newSelectedIds.delete(id);
        setSelectedIds(newSelectedIds);
    };

    const handleBulkDelete = () => {
        if (selectedIds.size > 0) {
            onBulkDelete(Array.from(selectedIds));
            setSelectedIds(new Set());
        }
    };

    const isAllSelected = filteredEvents.length > 0 && selectedIds.size === filteredEvents.length;
    const countText = `Toont ${filteredEvents.length} van ${events.length} events`;

    return (
        <div>
            <ViewHeader
                title="Events"
                countText={countText}
                onAddNew={handleAddNew}
                onImport={() => setIsImportModalOpen(true)}
                onSearch={setSearchTerm}
                searchTerm={searchTerm}
                hasEditPermissions={hasEditPermissions}
            />

            {selectedIds.size > 0 && hasEditPermissions && (
                 <div className="bg-indigo-50 border border-indigo-200 p-3 rounded-lg mb-4 flex items-center justify-between">
                    <span className="text-indigo-800 font-semibold">{selectedIds.size} geselecteerd</span>
                    <button onClick={handleBulkDelete} className="bg-red-500 text-white px-3 py-1 rounded-md text-sm hover:bg-red-600 flex items-center space-x-1">
                        {icons.trash} <span>Verwijderen</span>
                    </button>
                </div>
            )}

            <div className="bg-white shadow-md rounded-lg overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200">
                    <thead className="bg-gray-50">
                        <tr>
                            <th className="px-4 py-3"><input type="checkbox" checked={isAllSelected} onChange={handleSelectAll} /></th>
                            <SortableTh column={{ key: 'eventName', header: 'Event Naam' }} sortConfig={sortConfig} requestSort={requestSort} />
                            <th className="px-6 py-3 text-left">Uitvoeringen</th>
                            <th className="px-6 py-3 text-right">Acties</th>
                        </tr>
                    </thead>
                    <tbody className="bg-white divide-y divide-gray-200">
                        {filteredEvents.map(ev => (
                            <tr key={ev.id} className={selectedIds.has(ev.id) ? 'bg-indigo-50' : ''}>
                                <td className="px-4 py-4"><input type="checkbox" checked={selectedIds.has(ev.id)} onChange={(e) => handleSelectOne(e, ev.id)} /></td>
                                <td className="px-6 py-4 whitespace-nowrap font-medium">{ev.eventName}</td>
                                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                    {(ev.executionDetails || []).map((ex, i) => (
                                        <div key={i}>{ex.performanceTitle} @ {ex.locationName} ({ex.dateTime})</div>
                                    ))}
                                </td>
                                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                                    {hasEditPermissions && <>
                                        <button onClick={() => handleEdit(ev)} className="text-indigo-600 hover:text-indigo-900 mr-4">{icons.edit}</button>
                                        <button onClick={() => onDelete(ev.id)} className="text-red-600 hover:text-red-900">{icons.trash}</button>
                                    </>}
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>

            {isModalOpen && (
                <EventForm
                    event={editingEvent}
                    executions={executions}
                    performances={performances}
                    locations={locations}
                    onClose={() => setIsModalOpen(false)}
                    onSave={(data) => {
                        if (editingEvent) onUpdate(editingEvent.id, data);
                        else onAdd(data);
                        setIsModalOpen(false);
                    }}
                />
            )}
            {isImportModalOpen && (
                <GenericImportModal
                    title="Events"
                    requiredColumns={['eventName', 'executionIds']}
                    onClose={() => setIsImportModalOpen(false)}
                    onImport={(data) => onBulkAdd(data.map(item => ({...item, executionIds: item.executionIds.split(';')})))}
                />
            )}
        </div>
    );
}

export default EventsView;
